  /* Slider.css */
.slider-container {
  width: 80%;
  margin: 20px auto;
  text-align: center;
}

.slider {
  width: 100%;
  height: 10px;
  -webkit-appearance: none;
  appearance: none;
  outline: none;
  border-radius: 5px;
  background: #d3d3d3; /* Slider track color */
  margin-bottom: 10px;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: #4caf50; /* Slider thumb color */
  cursor: pointer;
}

span {
  font-size: 18px;
}

  