.hedaer {
    float: right !important;
    margin: 40px 0 20px 0 !important;
    padding: 10px 0px 0 0;
    width: 320px !important;
    z-index: 9999;
}
.hedaer-inner {
    background: #3e07f4 none repeat scroll 0 0;
    border-radius: 3px;
    display: inline-block;
    margin-bottom: 25px;
}
.hedaer ul{margin:0; padding:0;}
.hedaer li{list-style: none;}
.hedaer li:first-child{border:none;}
.hedaer ul li {
    border-top: 1px solid #5e08e8;
    float: left;
    list-style: outside none none;
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;
}
.hedaer ul li:hover{background: #3507ed;}
.hedaer h2 {
    background: #f1f508 none repeat scroll 0 0;
    font-size: 18px;
    margin: -10px 0 0 0;
    padding: 0 15px;
}
.hedaer ul li a {
    
    color: #fff;
    float: left;
    font-size: 13px;
    margin: 0;
    padding: 10px 0 10px 40px;
    text-decoration: none;
    text-transform: uppercase;
    width: 85%;
    cursor: pointer;
}
.hedaer .sub-menu {
    background: #380aed;
    display: none;
    float: left;
    left: -320px;
    position: absolute;
    width: 320px;
    z-index: 9999;
    top: 0;
}
.hedaer .sub-menu li{border-color:#4c0cef; list-style: none;}
.hedaer .sub-menu li a{color: #fff }
.hedaer .sub-menu li a:hover, .hedaer .sub-menu li:hover a{color: #fff}
.hedaer ul li:hover .sub-menu{display: block;}
.hedaer ul li:hover ul li .sub-menu {display: none;}
.hedaer ul li ul li:hover .sub-menu {display: block;}


.tags{padding: 25px 0}
.tags a {background: #8f278f;color: #fff; display: inline-block; margin: 0 0 2px; padding: 5px 22px 7px; text-decoration: none; text-transform: uppercase;}
.tags a:hover, .tags a:focus{color: #fff}
#Testimonials .item h2 {
    font-size: 16px;
    font-style: italic;
    font-weight: bold;
    line-height: normal;
    margin: 0;
}


.socialmedia ul{margin: 0; padding: 0}
.socialmedia ul li{margin: 0 10px; padding: 0; list-style: none; display: inline-block;}



.course-list .owl-nav {
    margin: 3px 0 0;
    position: absolute;
    right: 85px;
    top: -54px;
}
.course-list .owl-next  {
    /* background: url(images/right1.png) no-repeat left top !important; */
    font-size: 0;
    width: 24px;
    display: inline-block;
    height: 26px;
}
.course-list .owl-prev {
    /* background: url(images/left1.png) no-repeat left top!important; */
    font-size: 0;
    width: 24px;
    display: inline-block;
    height: 26px;
}
.course-list .owl-item{padding-bottom: 2px}
.page-template-full-template-events .hedaer,
.page-template-full-template-classroom .hedaer,
.page-template-full-template-online .hedaer,
.page-template-full-template-all-courses .hedaer, 
.page-template-template-combo-courses .hedaer,
.page-template-success-template  .hedaer{ margin: 0 0 20px 0!important;}
.footer_slider {
    background: #fff none repeat scroll 0 0;
    border-bottom: 1px solid #e5e5e5;
    border-top: 1px solid #e5e5e5;
    color: #fff;
    padding: 45px 0;
}