
.banner-wrapper {
    background: url(http://www.pixelstalk.net/wp-content/uploads/2016/04/landscape-wallpaper-hd-backgrounds.jpg) no-repeat center top / cover; 
    min-height: 352px;
    color: #fff;
  }
  .banner-caption {
    padding: 100px 0 0 0;
  }
  .banner-caption h2 {
    font-size: 36px;
  }
  .banner-caption p {
    font-size: 18px;
    color: #fff;
    margin-bottom: 25px;
    line-height: 25px;
  }
  .banner-caption a {
    background: #ee1d23;
    color: #faf8f8;
    display: inline-block;
    padding: 7px 15px;
    text-decoration: none;
  }
.img {
    position:fixed;
    width: 100%;
  }
  .text-overlay {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: left;
    color: white;

  }
  .marquee-header {
    background: #f5f861;
    color: #000;
    margin: 400px 0 0 0;
    padding: 8px 20px 4px 20px;
    position: relative;
    z-index: 9 !important;
  }

  .header-features {
    background: #fff;
    display: inline-block;
    box-sizing: border-box;
    margin: 1px 0 30px 0;
    padding: 20px 2%;
    width: 100%;
    -webkit-box-shadow: 0 6px 5px -7px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0 6px 5px -7px rgba(0, 0, 0, 0.75);
    box-shadow: 0 6px 5px -7px rgba(0, 0, 0, 0.75);
  }
  .header-features img {
    float: left;
    margin-right: 10px;
  }
  .header-features span.text {
    float: left;
  }